.minus-button {
  position: relative;
  top: 11px;
  width: 30px;
  background: #e75e26;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
  cursor: pointer;
}

.minus-button:hover {
  background: #111111;
}

.minus-button-fill {
  -webkit-transition: fill .7s;
  -o-transition: fill .7s;
  transition: fill .7s;
}



